import React from 'react'

const PrmanPatrComp = ({student,getClass}) => {
  return (
    <div className="container">
        <h1>बाल भारती विद्यापीठ उच्च माध्यमिक विद्यालय, सुजानगढ़</h1>
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <h2>अध्यनरत प्रमाण पत्र </h2>
        <b></b>
        <b></b>
        <p>प्रमाणित किया जाता है कि {student.gender === 'Girl' ? "छात्रा" : "छात्र"} {student.name} जिसके पिता का नाम {student.fathername} जो इस विद्यालय में कक्षा {getClass(student.class)} {student.gender === 'Girl' ? "की" : "का"} नियमित {student.gender === 'Girl' ? "छात्रा" : "छात्र"} है । जिसकी  जन्म तिथि {student.dob} SR No. {student.srno}  हैं।
          <br />बाल भारती परिवार इनके उज्जवल भविष्य की कामना करता है। </p>
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <div className="principal-sec">
          <p className="principal" onClick={window.print}>प्रधानाचार्य</p>
        </div>
      </div>
  )
}

export default PrmanPatrComp