import React from 'react'
import "./WrongSR.css"

const WrongSR = () => {
    const goToHome=()=>{
        window.location.href='/';
    }
  return (
    <div className='wrong-sr'>
    <div className="wrong-sr-container">
        <h1>The SR No. You mentioned is not valid</h1>
        <p>please Check Before you enter</p>
        <button onClick={goToHome}>Back to home</button>
    </div>
    </div>
  )
}

export default WrongSR