import React from 'react'

const SearchContainer = () => {
    const getDocPage=()=>{
        let seachSr=document.getElementById('srno-input').value;
        window.location.href=`/getdoc/${seachSr}`;
    }
  return (
    <div className="search-container">
        <h2>Enter Student's Sr No</h2>
        <input type="text" name="srno" id="srno-input" placeholder='SR No'/>
        <button className='btn' onClick={getDocPage}>Get Doc.</button>
      </div>
  )
}

export default SearchContainer