import React, { useState, useEffect } from 'react';
import Loading from 'react-loading-components';
import PrmanPatrComp from './component/PrmanPatrComp';
import WrongSR from './component/WrongSR';

const PrmanPatrDoc = () => {
  let searchSRno = window.location.pathname.split('/', 3)[2]
  const [student, setStudent] = useState({
    srno: <Loading type='spinning_circles' width={20} height={20} fill='#000' />,
    name: <Loading type='spinning_circles' width={20} height={20} fill='#000' />,
    fathername: <Loading type='spinning_circles' width={20} height={20} fill='#000' />,
    dob: <Loading type='spinning_circles' width={20} height={20} fill='#000' />,
    class: <Loading type='spinning_circles' width={20} height={20} fill='#000' />,
    gender: <Loading type='spinning_circles' width={20} height={20} fill='#000' />
  })
  const [validstudent, setValidstudent] = useState(true)
  useEffect(() => {
    getData();
  }, [])
  const getData = async () => {
    const response = await fetch('https://script.google.com/macros/s/AKfycbw2Y3s2PHBxRgnJBYR0AkK31cLu1LagXxhYnQ0HCH3ItQHwZKnCCZJhJLiosCKmB-kg/exec')
    const data = await response.json();
    const singleStudent = data.data.find(o => o.srno == searchSRno);
    console.log(singleStudent)
    if (singleStudent !== undefined) {
      setStudent({
        srno: singleStudent.srno,
        name: singleStudent.name,
        fathername: singleStudent.fathername,
        dob: singleStudent.dob,
        class: singleStudent.class,
        gender: singleStudent.gender
      })
      setValidstudent(true)
    }
    else {
      
      setStudent({
        srno: "No Student",
        name: "No Student",
        fathername: "No Student",
        dob: "No Student",
        class: "No Student",
        gender: "No Student"
      })
      setValidstudent(false)
    }
  }

  // Function for returning Class
  const getClass = (studentClass) => {
    switch (studentClass) {
      case 'First':
        return '1';
        break;
      case 'Second':
        return '2';
        break;
      case 'Third':
        return '3';
        break;
      case 'Fourth':
        return '4';
        break;
      case 'Fifth':
        return '5';
        break;
      case 'Sixth':
        return '6';
        break;
      case 'Seventh':
        return '7';
        break;
      case 'Eigth':
        return '8';
        break;
      case 'Ninth':
        return '9';
        break;
      case 'Tenth':
        return '10';
        break;
      case 'Eleventh':
        return '11';
        break;
      case 'Twelth':
        return '12';
        break;
      default:
        return <Loading type='spinning_circles' width={20} height={20} fill='#000' />;
    }

  }

  




  return (
    <>
      {validstudent===true?<PrmanPatrComp student={student} getClass={getClass}/>:<WrongSR/>}
    </>
  );
}

export default PrmanPatrDoc